.timer-calc {
	display: -ms-flexbox;
	display: flex;
	width: 45em;
	margin: 15px auto;
	-ms-flex-align: baseline;
	    align-items: baseline;
	-ms-flex-pack: left;
	    justify-content: left;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
}

.timer-calc__text {
	width: 100%;
	text-align: left;
	margin-top: 15px;
	margin-bottom: 15px;
}

.timer-calc__start,
.timer-calc__end {
	font-size: 14pt;
	width: 4em;
	border: 0;
	border-bottom: 1px solid #ccc;
	text-align: right;
	padding: 0;
	margin: 0;
	margin-right: 1em;
}

.timer-calc__result {
	font-size: 14pt;
	text-align: left;
}



